<template>
    <div class="home">
        <img alt="Vue logo" src="../assets/logo.png" />
        <div>{{ obj }}</div>
        <el-button @click="goAnimTab">跳转到动画页</el-button>
    </div>
</template>

<script>
import { ref, reactive, computed, getCurrentInstance } from "vue";
export default {
    name: "Home",
    props: ["value"],
    setup: (props, context) => {
        const { ctx } = getCurrentInstance();
        const count = ref(0); // 定义响应式数据count
        const num = ref(1); // 定义响应式数据num
        const dialogVisible = ref(true);
        const objData = {
            name: "erha",
            age: "1",
            skill: "拆家"
        };
        const val = props.value;
        const obj = reactive(objData); // 定义响应式数据obj
        const name = computed(() => objData.name);

        const goAnimTab = () => {
            ctx.$router.push("/Anim");
        };
        return {
            count,
            num,
            obj,
            dialogVisible,
            name,
            goAnimTab
        };
    }
};
</script>

